
/* General */
body {
    text-align: center;
}
/* Header */
.card {
    .card-title {
    display: block;
    font-size: 36px;
    margin-bottom: 5px;
    font-family: 'Lobster';
    }
}

a {
    margin-bottom: 20px;
}

/* Input */

textarea {
    resize: none;
    height: 670px;
    max-height: 670px;
    margin-right: 10px;
}

/* Output */
.output {
    margin-left: 10px;
    height: 670px;
    overflow: auto;
    text-align: left;
    span {
        height: 660px;
        max-height: 660px;  
    }
}


